<template>
  <div class="selectdown">
    <el-scrollbar height="800px">
    <el-table  class="table-content"
        :data="dataList"
        border
        style="width: 100%;"
    >
      <el-table-column
      label="序号"
      fixed="left"
      width="50"          
      align="center"
      color="black"
      >
      <template v-slot="scope">
          {{ scope.$index+1 }}
      </template>
      </el-table-column>

      <el-table-column
      label="课程编号"
      align="center"
      color="black"
      sortable
      prop="courseNum" />

      <el-table-column
      label="课程名称"
      align="center"
      color="black"
      prop="courseName" />

      <el-table-column
      label="课程教师"
      align="center"
      color="black"
      sortable
      prop="courseteacher" />

      <el-table-column
      label="课程信息"
      align="center"
      color="black"
      sortable
      prop="courseinfo" />

      <el-table-column
      label="课程上课日"
      align="center"
      color="black"
      sortable
      prop="courseday" />

      <el-table-column
      label="课程上课节次"
      align="center"
      color="black"
      sortable
      prop="courseorder" />

      <el-table-column
      label="课程学分"
      align="center"
      color="black"
      sortable
      prop="coursescore" />

      <el-table-column
      label="课程选课结束时间"
      align="center"
      color="black"
      sortable
      prop="courseselectend" />

      <el-table-column
          label="操作"
          align="center"
          color="black"
      >
      <template v-slot="scope">
        <el-popconfirm title="确定要退选该课程吗？" @confirm="doDelete(scope.row.selectcourseId,scope.row.courseselectend)" @cancel="cancelEvent">
        <template #reference>
          <el-button type="primary" :icon="Delete" size="mini" >退选</el-button>
        </template>
        </el-popconfirm>
      </template>
      </el-table-column>
    </el-table>
    </el-scrollbar>
  </div>
</template>

<script>
import { ElMessage } from 'element-plus'
import {getselectCourseList,selectcourseDelete} from '@/service/genServ.js'
export default {
  name: 'selectdown',
  data() {
    return {
      dataList:[]
    }
  },
  created() {
    this.doQuery();
  },
  methods: {
    doQuery() {
       getselectCourseList({'sid':localStorage.getItem('sid')}).then(res => {
        this.dataList = res.data;
        console.log(this.dataList);
      })
    },
  

    doDelete(selectcourseId,courseselectend) {
      var d1 = new Date();
      var d2 = new Date(courseselectend);
      if (d1.getTime()>d2.getTime()) {
        ElMessage({
          message: '选课时间已过无法退选',
          type: 'warnning',
        })
      } else{
       selectcourseDelete({'selectcourseId':selectcourseId}).then(res => {
        console.log(res)
        ElMessage({
          message: '退选成功！',
          type: 'success',
        })
        this.doQuery();
      })
      }
    },

  }
}
</script>

<style scoped>
.selectdown{
  width: 1650px;
  position:absolute;
  top:100px;
  left:225px;
}
</style>
